.top-buttons {
  position: fixed;
  top: 1em;
  right: 1em;
  display: flex;
  gap: 8px;
  z-index: 999;
  justify-content: flex-start;
  direction: rtl;
}

.language-select {
  border: 1px solid #aedbe8;
  background-color: #25325b;
  color: #aedbe8;
  border-radius: 4px;
  padding: 4px 8px;
}
.contact-me {
  border: 1px solid #aedbe8;
  background-color: #25325b;
  color: #aedbe8;
  border-radius: 4px;
  padding: 4px 8px;
  text-decoration: none;
}

html,
body {
  padding: 0;
  margin: 0;
  background: #40255b;
  font-family: "Rubik", sans-serif;
}

main {
  height: 300vh;
}

p {
  /* font-family: helvetica, sans-serif; */
  color: #eee;
}

.banner {
  height: 100vh;
  background: #bbe8f6;
}

.title {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.title h1 {
  font-size: 6vw;
  margin-bottom: 0;
}

.title .sub {
  font-size: 4vw;
}

.about-me {
  padding: 32px 10vw;
  color: white;
  font-size: 20px;
}

.about-me .more {
  font-weight: 500;
}
.about-me .more:hover {
  text-decoration: underline;
}

.services {
  display: flex;
  gap: 32px;
  overflow-x: auto;
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
  padding: 64px 10vw;
  background-color: #74a7bd;
  justify-content: center;
}

.services::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}

.services .img-wrapper {
  height: 300px;
  overflow: hidden;
  position: relative;
  border-radius: 8px;
  transition: 200ms ease-out;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0);
  min-width: 200px;
}

.services .img-wrapper:hover {
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.5);
}

.services .img-wrapper img {
  height: 150%;
  background-size: cover;
}

.services .img-wrapper .desc {
  background-color: rgba(0, 0, 0, 0);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  color: rgba(255, 255, 255, 0);
  transition: 200ms ease-out;
  display: flex;
  justify-content: center;
  align-items: center;
}

.services .desc:hover {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}

footer {
  color: white;
  padding: 32px 10vw;
}
